.exercise2 {
    display: flex;
    padding: 100px;
    min-height: 50vh;
    justify-content: center;
    align-items: center;
}

.red-button {
    padding: 20px 40px;
    color: crimson;
    border-radius: 40px;
    border: 0;
    margin: 10px;
    text-transform: uppercase;
    background: none;
    box-shadow: inset 0 0 0 3px crimson;
    cursor: pointer;
}

